@import '../../assets/variables.module.scss';

.zol-zoom-controls{
    position: absolute;
    bottom: 20px;
    right: 20px;

    > span{
        display: block;
        width: 25px;
        height: 25px;
        line-height: 25px;
        font-size: 18px;
        text-align: center;
        font-weight: bold;
        background-color: $color-white;
        box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
        cursor: pointer;
        color: $color-dark;
    }
    .zol-zoom-in{
        border-radius: 6px 6px 0px 0px
    }
    .zol-zoom-out{
        border-radius: 0px 0px 6px 6px
    }
    .zol-zoom-reset{
        border-radius: 6px;
        margin-top: 7px;
        font-size: 14px;
    }
    .zol-disabled{
        color: $color-light;
        cursor: default;
    }
}