@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,600;0,900;1,400&display=swap');
@import "./assets/variables.module.scss";


html,
body {
    margin: 0;
    font-family: "Poppins", Helvetica, Arial, Verdana, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: $color-dark;
    color: $color-white;
}

body, input, button {
    font-size: 16px;
}

.zol-cpa-root{
    position: relative;
    width: 100%;
}

.zol-map{
    position: absolute;
    width: 100%;
}

.zol-cpa-header{
    position: relative;
    text-align: center;
    max-width: 60%;
    margin: 50px auto 20px;

    h1{
        font-weight: 900;
        font-size: 52px;
    }

    p.zol-cpa-select-note{
        font-weight: 400;
        font-size: 24px;
        font-style: italic;
        text-shadow: 1px 1px 6px black;
    }

    a{
        color: white;
    }

    span.zol-cpa-show-intro{
        text-decoration: underline;
        cursor: pointer;
        font-style: italic;
    }

}

p.zol-markdown-text-text{
    margin: 30px 0px;
}

p.zol-markdown-text-list{
    margin: 5px;

    &:before{
        content: '';
        display: inline-block;
        position: relative;
        left: 0px;
        top: 7px;
        width: 25px;
        height: 25px;
        background: transparent url('./assets/icons/bullet.svg') center center no-repeat;
        background-size: 15px;
    }
}

p.zol-markdown-text-header{
    margin: 50px 0px -20px;
    font-weight: bold;
    font-size: 110%;
}

.zol-cpa-map-row{
    position: relative;
    width: 100%;
}

.zol-cpa-map-col{
    float: left;
    max-width: 300px;
    &:after{
        content: '';
        display: block;
        clear: both;
    }

    .zol-cpa-list-item{
        clear: both;
        display: block;
        height: 90px;
        line-height: 90px;
        margin: 10px 0px;
        font-weight: 600;
        font-size: 17px;
        cursor: pointer;
        > svg{
            float: right;
            margin-left: 10px;
            transition: transform 0.3s;
        }
        >span{
            display: inline-block;
            width: calc(100% - 90px);
            line-height: 1.5em;
            vertical-align: middle;
            margin-top: -15px;
        }

        &:hover{
            font-weight: 900;

            >svg{
                transform: rotate(5deg) scale(1.1);
            }
        }

        &.zol-active{
            span{
                font-weight: 900;
                text-decoration: underline;
            }

            >svg{
                transform: scale(1.2);
            }
        }
        &.zol-inactive{
            opacity: 0.6;
            &:hover{
                opacity: 1;
            }
        }
    }
}
.zol-cpa-map-unions{
    margin-left: 15px;
    text-align: right;
}
.zol-cpa-map-recs{
    float: right;
    margin-right: 15px;
    .zol-cpa-list-item{
        > svg{
            float: left;
            margin-left: 0px;
            margin-right: 10px;
        }
    }
}

.zol-map-tooltip{
    background-color: rgba(255, 255, 255, 0.85);
    color: $color-dark;
    pointer-events: none;
    width: 200px;
    padding: 8px;
    margin-top: -43px;
    font-size: 14px;
    cursor: pointer;
    border: 1px solid $color-light;
    z-index: 1;

    &.zol-map-tooltip-right{
        margin-left: 2px;
    }
    &.zol-map-tooltip-left{
        margin-right: 2px;
        padding: 5px 20px 5px 10px;
    }

    h2 {
        margin: 0px 0px 4px 0px;
        font-size: 16px;
        font-weight: normal;
    }
    dl{
        margin: 0px;
        > dt{
            // font-weight: bold;
            font-size: 90%;
            color: $color-dark;
        }
        >dd{
            margin: 0px 0px 10px 10px;
        }
    }
}

.cpa-map-legend{
    position: absolute;
    width: 300px;
    padding: 0px 20px 10px;
    background-color: rgba(255, 255, 255, 0.1);
    color: $color-white;

    > h2{
        font-weight: normal;
        font-size: 20px;
    }

    ul{
        margin: 0px;
        padding: 0px;
        list-style: none;

        > li{
            padding: 10px 0px;
            .legend-box{
                float: left;
                width: 30px;
                height: 30px;
                border-radius: 50%;
                border: 1px solid $color-white;
                margin: -3px 10px 0px 0px;
            }
        }
    }
}

.zol-cpa-details{
    position: absolute;
    width: calc(100% - 700px);
    color: $color-black;

    .zol-cpa-details-background{
        position: absolute;
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
        line-height: 0;
    }

    .zol-cpa-details-content{
        position: absolute;
        top: 0px;
        padding: 50px 30px 0px;

        .zol-cpa-details-close{
            background: transparent url('./assets/icons/close-outline.svg') right center no-repeat;
            background-size: 30px;
            padding-right: 35px;
            font-size: 120%;
            float: right;
            margin-top: 10px;
            cursor: pointer;
        }

        h2{
            margin-top: 50px;
        }

        p.zol-markdown-text-list{
            position: relative;
            padding-left: 30px;
            &:before{
                position: absolute;
                left: -6px;
                top: 2px;
                background-size: 20px;
            }
        }

        span.zol-markdown-text-underline{
            text-decoration: underline;
        }

        .zol-cpa-button{
            float: right;
            background: $color-light url('./assets/icons/chevron-right-outline.svg') calc(100% - 10px) center no-repeat;
            background-size: 20px;
            padding: 10px 30px 10px 20px;
            color: $color-black;
            text-decoration: none;
            margin: 20px 0px;
        }
        .zol-cpa-button-green{ background-color: $color-green; }
        .zol-cpa-button-purple{ background-color: $color-purple; }
        .zol-cpa-button-orange{ background-color: $color-orange; }
        .zol-cpa-button-pink{ background-color: $color-pink; }
        .zol-cpa-button-yellow{ background-color: $color-yellow; }

        &:after{
            content: '';
            display: block;
            clear: both;
        }
    }
}

.zol-cpa-map-bottom{
    position: absolute;
    left: 30px;
    right: 30px;
}

.zol-cpa-footer{
    position: absolute;
    width: 100%;
    font-size: 80%;

    .zol-cpa-logo{
        float: left;
        width: 300px;

        >svg{
            width: 100%;
        }
    }

    .zol-cpa-footer-text{
        position: absolute;
        left: 440px;
        top: 50px;
        right: 30px;
        bottom: 50px;
        border-left: 1px solid rgba(255, 255, 255, 0.3);
        padding-left: 60px;
        color: rgba(255, 255, 255, 0.7);

        >img{
            margin-top: 10px;
            margin-left: -5px;
            width: 340px;
        }
    }
}

@media screen and (max-width: 600px) {
    .zol-cpa-header{
        max-width: calc(100% - 20px);

        p.zol-cpa-select-note{
            font-size: 120%;
        }
    }
    .zol-map{
        position: relative;
    }

    .zol-cpa-map-row{
        position: static;
    }

    .zol-cpa-map-col{
        float: none;
        margin: 0px auto;
        max-width: calc(100% - 20px);
        text-align: left;

        h2{
            font-size: 130%;
        }

        .zol-cpa-list-item{
            height: auto;
            line-height: 1.5em;
            padding: 0px 0px 20px;

            > svg{
                float: left;
                margin: 3px 10px 0px 0px;
            }
            > span{
                margin: 0px;
                width: calc(100% - 40px);
                vertical-align: top;
            }
        }
    }

    .cpa-map-legend{
        margin-left: -30px;
        h2 {
            font-size: 16px;
            margin: 10px 0px;
        }

        > ul > li {
            display: inline-block;
            margin-right: 10px;
            padding: 0px;

            > .legend-box{
                width: 15px;
                height: 15px;
                margin-top: 5px;
            }
        }
    }

    .zol-cpa-details{
        position: absolute;
        width: calc(100% - 20px);
        margin-top: 35px;
    
        .zol-cpa-details-content{
            padding: 40px 15px 0px;
    
            .zol-cpa-details-close{
                background-size: 30px;
                padding-right: 35px;
                font-size: 100%;
            }
    
            h2{
                margin-top: 40px;
                font-size: 110%;
            }
    
            p.zol-cpa-details-list{
                padding-left: 20px;
                background-size: 14px;
            }
        }
    }

    .zol-cpa-map-bottom{
        position: static;
    }
    .zol-cpa-footer{
        position: static;
        padding: 0px 10px;

        .zol-cpa-logo{
            float: none;
            display: block;
            width: 100%;
            max-width: 300px;
        }

        .zol-cpa-footer-text {
            position: static;
            border-left: 0px;
            padding: 30px 0px;
        }
    }

    .zol-cpa-select {
        position: relative;

        select.zol-single-select {
            background-color: #451F32;
            border: 1px solid rgba(255, 255, 255, 0.3);
            color: white;
            padding: 10px 30px;
        }
        &:after, &:before{
            display: block;
            content: '';
            position: absolute;
            top: 1px;
            bottom: 1px;
            width: 30px;
            pointer-events: none;
        }
        &:after{
            right: 1px;
            background: #451F32 url('./assets/icons/chevron-down.svg') center center no-repeat;
        }
        &:before{
            left: 1px;
            background: #451F32 url('./assets/images/diamond-mobile.svg') center center no-repeat;
        }

        &.zol-cpa-select-recs{
            &:before{
                background: #451F32 url('./assets/images/diamond-mobile-green.svg') center center no-repeat;
            }
        }
    }
}