@import "../../assets/variables.module.scss";

.timeline{
    padding: 50px 250px;
    height: 200px;

    > h2{
        font-weight: normal;
        text-align: center;
        margin: 0px 0px 50px;
    }

    > ol{
        list-style: none;
        padding: 0;
        margin: 0;

        > li {
            position: relative;
            display: inline-block;
            height: 20px;
            background-color: rgba(255, 255, 255, 0.2);
            margin: 0px 5px;

            .timeline-event{
                position: absolute;
            }

            &:after{
                left: 100%;
                top: 50%;
                border: solid transparent;
                content: "";
                height: 0;
                width: 0;
                position: absolute;
                pointer-events: none;
                border-color: rgba(255, 255, 255, 0);
                border-left-color: rgba(255, 255, 255, 0.2);
                border-width: 10px;
                margin-top: -10px;
            }

            &:before{
                left: 0px;
                top: 50%;
                border: solid transparent;
                content: "";
                height: 0;
                width: 0;
                position: absolute;
                pointer-events: none;
                border-color: rgba(0, 0, 0, 0);
                border-left-color: $color-dark;
                border-width: 10px;
                margin-top: -10px;
            }

            &:nth-child(odd){
                background-color: rgba(0, 0, 0, 0.3);
                &:after{
                    border-left-color: rgba(0, 0, 0, 0.3);
                }
            }

            .timeline-year{
                position: relative;
                display: block;
                left: calc(50% - 12px);
                top: -30px;
                height: 60px;
                width: 20px;
                text-indent: -400px;
                overflow: hidden;
            }

            &.zol-enabled{
                cursor: pointer;
                .timeline-year{
                    text-indent: 0px;
                    width: 50px;

                    &:after{
                        content: '';
                        position: absolute;
                        display: block;
                        top: 35px;
                        left: 12px;
                        width: 10px;
                        height: 10px;
                        border-radius: 50%;
                        background-color: white;
                    }
                }
            }

            .timeline-event{
                position: absolute;
                width: 280px;
                left: calc(50% - 145px);
                top: 35px;
                background-color: $color-white;
                color: $color-black;
                text-align: center;
                font-size: 14px;
                padding: 10px;
                cursor: default;

                &:before {
                    bottom: 100%;
                    left: 50%;
                    border: solid transparent;
                    content: '';
                    height: 0;
                    width: 0;
                    position: absolute;
                    pointer-events: none;
                    border-color: rgba(255, 255, 255, 0);
                    border-bottom-color: $color-white;
                    border-width: 10px;
                    margin-left: -10px;
                }

                p.zol-markdown-text-text{
                    margin: 0px;
                }
            }
        }
    }

    .timeline-navigation{
        display: block;
        position: absolute;
        width: 380px;
        top: 30px;
        left: calc(50% - 185px);
        z-index: 2;

        > span{
            display: block;
            position: absolute;
            width: 50px;
            height: 50px;
            fill: white;
            cursor: pointer;
            opacity: 0.85;
            transition: opacity 0.2s;

            > svg{
                width: 50px;
            }

            &.previous{
                left: 0px;
            }

            &.next{
                right: 0px;
            }

            &:hover{
                opacity: 1;
            }

            &.zol-disabled{
                opacity: 0.2;
                cursor: default;
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .timeline{
        padding: 30px 15px;

        > ol{
            position: relative;
    
            > li {
                position: static;
                display: inline-block;
                height: 5px;
                background-color: rgba(255, 255, 255, 0.4);
                margin: 0px 1px;
    
                .timeline-event{
                    position: absolute;
                }
    
                &:after, &:before{
                    display: none;
                }

                &:nth-child(odd){
                    background-color: rgba(255, 255, 255, 0.4);
                }

                .timeline-year{
                    top: -30px;
                }
        
                &.zol-enabled{
                    cursor: pointer;
                    .timeline-year{
                        text-indent: -400px;
                        width: 50px;
                        left: calc(50% - 20px);
        
                        &:after{
                            content: '';
                            position: absolute;
                            display: block;
                            top: 27px;
                            left: 15px;
                            width: 10px;
                            height: 10px;
                            border-radius: 50%;
                            background-color: white;
                            opacity: 0.7;
                        }
                    }

                    &.zol-selected .timeline-year{
                        text-indent: 0px;

                        &:after{
                            opacity: 1;
                            border: 5px solid white;
                            background-color: $color-dark;
                            top: 22px;
                            left: 10px;
                        }
                    }
                }
        
                .timeline-event{
                    position: absolute;
                    width: calc(100% - 20px);
                    left: 0px;
        
                    &:before {
                        display: none;
                    }

                    h3{
                        margin: 0px;
                    }
                }

                .timeline-navigation{
                    width: calc(100%);
                    top: 30px;
                    left: 0px;
                    z-index: 2;
            
                    > span{
                        fill: black;

                        > svg{
                            margin: 5px;
                            width: 40px;
                        }
                    }
                }
            }
        }

    }
}